<template>
  <div class="row bg-hui h-full content-wrapper justify-content-md-center">
    <div class="col-sm-12 ready-title text-center">
      请选择一位客户
    </div>
    <div class="col-sm-6 bg-white br-horizonS def-shadow">
      
      <listing-plus-ready-member :member="memberSelect"></listing-plus-ready-member>

      <div class="row mt-2">
        <div class="col-sm-12">
          <div class="input-group mb-3 members-search-panel">
            <input type="text" class="form-control" placeholder="请输入名称或编号" aria-label="请输入名称或编号" aria-describedby="memberSearch">
            <div class="input-group-append">
              <button class="btn btn-outline-info" type="button" id="memberSearch">筛选</button>
            </div>
          </div>
          <div class="br-horizonS members-panel br-table">
            <table class="table member-table">
              <tr v-for="m in members" :key="m.id" :class="{ 'member-choise': m.id == memberSelect.id }" @click="memberChoise(m.id)" class="hand">
                <td>{{ m.num }}</td>
                <td>{{ m.named }}</td>
                <td>{{ m.addr }}</td>
              </tr>
            </table>
          </div>
        </div>
      </div>

      <div class="text-center mt-3">
        <button class="btn btn-main" @click="toListingPlus">
          下一步
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import ListingPlusReadyMember from './ListingPlusReadyMember.vue'
import service from '@/service'

export default {
  components: { ListingPlusReadyMember },
  name: 'ListingPlusReady',
  data() {
    return {
      members: service.members,
      memberSelect: service.members[0]
    }
  },
  methods: {
    memberChoise(id) {
      for(const m of this.members) {
        if(m.id == id) {
          this.memberSelect = m
        }
      }
    },
    toListingPlus() {
      tool.storage._set('memberSelect', JSON.stringify(this.memberSelect))
      
      this.$router.push({
        path: '/home/listing_plus',
        query: {
          member: this.memberSelect
        }
      })
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
.members-search-panel, .member-table
  margin: 0 !important

.members-panel
  height: 50vh
  border-top: none

.member-table tr:first-child > td
  border: none

.member-choise
  background: $primary
  color: white

.ready-title

</style>


